.seccionLibroVentas {
  margin-top: 1rem;
}
.seccionLibroVentas .buscador {
  display: flex;
  justify-content: space-around;
}
.seccionLibroVentas .buscador .botones button {
  margin-right: 2rem;
  border-radius: 7.5rem;
  border: none;
  padding: 1.5rem;
}
.seccionLibroVentas .buscador .botones button:hover {
  cursor: pointer;
  transform: scale(1.2);
}
.seccionLibroVentas main h2 {
  text-align: center;
}
.seccionLibroVentas main .tabla {
  border-collapse: collapse;
}
.seccionLibroVentas main .tabla table {
  width: 100%;
}
.seccionLibroVentas main .tabla table .tbody {
  text-align: center;
  font-size: 1.5rem;
}